<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue/swiper-vue";

import "swiper/swiper-bundle.css";

import { Pagination, Navigation, Autoplay } from "swiper";

export default {
  name: 'carousel',
  components: {
    Swiper, SwiperSlide
  },
  setup() {
    const onSwiper = () => {
      //useSwiper();
    };

    return {
      onSwiper,
      progressing: true,
      navigation: {
        clickable: true,
        nextEl: ".btnNext"
      },
      pagination: {
        clickable: true,
        type: 'fraction',
        el: '.wrapperFractionPagi',
        formatFractionCurrent: function(number) {
          return '0' + number
        },
        formatFractionTotal: function(number) {
          return '0' + number
        },
        renderFraction: function(currentClass, totalClass) {
          return '<div class="fractCurrent ' + currentClass + '"></div>' + '<div class="fractProgress"><div class="prgoressionBar"></div></div>' + '<div class="fractTotal ' + totalClass + '"></div>'
        }
      },
      breakpoints: {
        320: {
            slidesPerView: 1,
          },
          1024: {
            slidesPerView: 2,
          },
      },
      autoplay: {
        delay: 5000,
        disableOnInteraction : false,
        pauseOnMouseEnter: true,
      },
      modules: [Pagination, Navigation, Autoplay],
    };
  },
  mounted() {
  },
  methods: {
    imagesReady: function(){
      this.$el.querySelector(".prgoressionBar").classList.add("progressMax")
    },
    onSlideChange: function() {
      //return this.progressing = false; // can't use this as swiper inject to dom after mount
      this.$el.querySelector(".prgoressionBar").classList.remove("progressMax");
    },
    onTransEnd: function() {
      //return this.progressing = true;
      this.$el.querySelector(".prgoressionBar").classList.add("progressMax")
    },
  },
  computed: {
  }
}
</script>