                      </div>
                      <div class="tags">Startup | Website | Application | Branding</div>
                    </div>
                    <div class="thumbBg bright movingRadial" data-bg-col="rgba(0,173,255,1)" data-bg-center="rgba(116,225,239,1)"></div>
                  </div>
                  <div class="artboard radial">
                    <div class="artboardImg">
                      <img src="@/assets/images/wd_lite_thumbs.png">
