                    <div class="thumbBg dark movingRadial" data-bg-col="rgba(45,63,89,1)" data-bg-center="rgba(87,101,122,1)"></div>
                  </div>

                </div>
              </div>
            </div>
          </li>
          <li class="col-1-2">
            <div class="wrapperProjectThumb single">
              <div class="">
                <div class="overflowThumb tr"><img src="@/assets/images/wd_pro_mix.png" alt="Whaledoo Pro"></div>
                <div class="projectThumb">
                  <div class="wrapperRationale radial">
                    <div class="rationale">
                      <div class="info">
                        <h3>Whaledoo PRO</h3>
                        <h2>Deep dive to the PRO</h2>
                      </div>
                      <div class="tags">Startup | Website | Application | Branding</div>
                    </div>
                    <div class="artboard">
                      <div class="artboardImg">
                        <img src="@/assets/images/wd_pro_thumbs.png">
                      </div>
                    </div>
                    <div class="thumbBg purple movingRadial" data-bg-col="rgba(75,70,114,1)" data-bg-center="rgba(110,100,170,1)"></div>
                  </div>
                </div>
              </div>
            </div>
          </li>
          <li class="col-1-2">
            <div class="wrapperProjectThumb single">
              <div class="">
                <div class="overflowThumb tr"><img src="@/assets/images/scoodepool_mix.png" alt="Scoodepool"></div>
                <div class="projectThumb">
                  <div class="wrapperRationale radial">
                    <div class="rationale">
                      <div class="info">
                        <h3>Scoopdepool</h3>
                        <h2>RM1 raffle e-commerce</h2>
                      </div>
                      <div class="tags">Startup | Website | Application | Branding</div>
                    </div>
                    <div class="artboard">
                      <div class="artboardImg">
                        <img src="@/assets/images/scoopdepool_thumbs.png">
                      </div>
                    </div>
                    <div class="thumbBg grey movingRadial" data-bg-col="rgba(54,56,70,1)" data-bg-center="rgba(76,80,100,1)"></div>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
        
